.app {
    text-align: center;
    min-width: 1000px;
}

.app-header {
    padding-top: 16px;
    padding-bottom: 16px;
    background-color: #ffdb91;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: black;
}

.left-title {
    font-family: 'Balsamiq Sans', sans-serif;
    font-size: calc(14px + 3vmin);
    flex-basis: 25%;
    min-width: 280px;
}

.right-title {
    font-size: calc(12px + 1vmin);
    flex-basis: 65%;
    margin-left: 20px;
    text-align: justify;
}

.main-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1000px;
    margin: auto;
}

.main-text {
    margin-top: 10px;
    font-size: calc(10px + 1vmin);
}

.charts-block {
    margin-top: 10px;
    margin-bottom: 10px;
}

.charts-names {
    font-family: 'Balsamiq Sans', sans-serif;
    font-size: calc(10px + 2vmin);
    margin-top: 20px;
}

.footer-block {
    font-family: 'Balsamiq Sans', sans-serif;
    border-top: 2px solid #f5e5bc;
    margin-top: 16px;
    color: #504a4a;
    font-size: 14px;
    padding-bottom: 12px;
    padding-top: 16px;
}
